import CanvasNest from 'canvas-nest.js';
import DragVerify from '@/components/common/DragVerify.vue'; //  引入滑动解锁组件

import { addUser } from '@/request/user.js'; // 配置

var config = {
  color: '230, 162, 60',
  // 线条颜色
  pointColor: '230, 162, 60',
  // 节点颜色
  opacity: 0.5,
  // 线条透明度
  count: 99,
  // 线条数量
  zIndex: -1 // 画面层级

};
export default {
  name: 'Register',
  components: {
    DragVerify: DragVerify
  },
  data: function data() {
    return {
      // 注册表单
      registerForm: {
        telephone: '',
        username: '',
        password: ''
      },
      // 注册表单校验规则
      registerFormRules: {
        username: [{
          required: true,
          message: '请输入用户名',
          trigger: 'blur'
        }],
        password: [{
          required: true,
          message: '请输入密码',
          trigger: 'blur'
        }, {
          min: 5,
          max: 20,
          message: '长度在 5 到 20 个字符',
          trigger: 'blur'
        }],
        telephone: [{
          required: true,
          message: '请输入手机号',
          trigger: 'blur'
        }, {
          min: 11,
          max: 11,
          message: '请输入11位手机号',
          trigger: 'blur'
        }]
      },
      isPassing: false,
      //  滑动解锁是否验证通过
      submitDisabled: true //  登录按钮是否禁用

    };
  },
  computed: {
    url: function url() {
      var _url = this.$route.query.Rurl; //  获取路由前置守卫中 next 函数的参数，即登录后要去的页面

      return _url ? _url : '/'; //  若登录之前有页面，则登录后仍然进入该页面
    }
  },
  watch: {
    //  滑动解锁验证通过时，若重新输入手机号、用户名或密码，滑动解锁恢复原样
    'registerForm.telephone': function registerFormTelephone() {
      this.isPassing = false;
      this.$refs.dragVerifyRef.reset();
    },
    'registerForm.username': function registerFormUsername() {
      this.isPassing = false;
      this.$refs.dragVerifyRef.reset();
    },
    'registerForm.password': function registerFormPassword() {
      this.isPassing = false;
      this.$refs.dragVerifyRef.reset();
    }
  },
  created: function created() {
    //  绘制背景图
    this.$nextTick(function () {
      var element = document.getElementById('registerBackground');
      new CanvasNest(element, config);
    });
  },
  methods: {
    /**
     * 滑动解锁完成 回调函数
     * @param {boolean} isPassing 解锁是否通过
     */
    updateIsPassing: function updateIsPassing(isPassing) {
      var _this = this;

      if (isPassing) {
        //  校验手机号
        this.$refs.registerForm.validateField('telephone', function (telephoneError) {
          if (telephoneError) {
            _this.submitDisabled = true;
          } else {
            _this.submitDisabled = false;
          }
        });
      } else {
        this.submitDisabled = true;
      }
    },

    /**
     * 注册按钮点击事件 表单验证&用户注册
     * @param {boolean} formName 表单ref值
     */
    submitForm: function submitForm(formName) {
      var _this2 = this;

      this.$refs[formName].validate(function (valid) {
        if (valid) {
          // 表单各项校验通过
          addUser(_this2.registerForm).then(function (res) {
            if (res.success) {
              _this2.$notify({
                title: '成功',
                message: '注册成功！已跳转到登录页面',
                type: 'success'
              });

              _this2.$refs[formName].resetFields();

              _this2.$router.replace({
                path: '/login'
              });
            } else {
              _this2.$message.error(res.message);
            }
          });
        } else {
          _this2.$message.error('请完善信息！');

          return false;
        }
      });
    }
  }
};